@mixin round-before
    &::before    
        content: ''
        position: absolute
        left: 0
        top: 50%
        transform: translateY(-50%)
        z-index: -1
        width: 36px
        height: 36px
        border-radius: 100%
        background-color: $main-color

@mixin form-label
    display: block
    height: 18px
    margin-bottom: 0
    position: absolute
    left: 12px
    padding: 0 8px
    background-color: $second-color
    font-size: 12px
    color: #000000    