@import 'lib/bootstrap-reboot.min'
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap')
@import 'base/variables'
@import 'base/mixins'

*
    margin: 0
    padding: 0
    box-sizing: border-box
    font-family: 'Poppins', sans-serif

.container
    max-width: 1140px
    margin: 0 auto
    position: relative

.divider
    height: 3px
    width: 60px
    margin: 25px auto 0 auto
    background-color: $main-color

.back-link
    display: none
    width: 40px
    height: 40px
    position: fixed
    z-index: 999
    bottom: 50px
    right: 50px

.back-img
    width: 100%
    height: 100%

    
@import 'blocks/promo'
@import 'blocks/hamburger'
@import 'blocks/menu'
@import 'blocks/title'
@import 'blocks/button'
@import 'blocks/about'
@import 'blocks/experience'
@import 'blocks/tools'
@import 'blocks/works'
@import 'blocks/price'
@import 'blocks/contacts'
@import 'blocks/media'
