.contacts
    padding: 120px 0 92px 0
    &__wrapper
        padding: 0 35px 0 52px
        display: grid
        grid-template-columns: 400px 585px
        justify-content: space-between
    &__img
        width: 100%
        height: 600px
        object-fit: cover
    &__title
        text-align: left
        padding-left: 12px
        &::before
            left: 0
            transform: translateY(0)
    &__subtitle
        margin-top: 6px
    &__divider
        margin: 4px 0 0 0
    &__descr
        margin-top: 20px
    &__socials
        margin-top: 20px
        display: flex
    &__socials-link
        width: 30px
        height: 30px
        margin-right: 20px
    &__socials-img
        max-width: 100%
        max-height: 100%
    &__form
        margin-top: 30px
        display: grid
        grid-template: 45px 190px 45px / repeat(2, 280px)
        justify-content: space-between
        row-gap: 28px
    &__label
        @include form-label
        top: -9px
    &__item
        position: relative
    &__input
        width: 100%
        height: 100%
        padding: 14px 20px 13px 20px
        font-size: 12px
        color: #000000
    &__item-text
        grid-column: 1/3
        position: relative
    &__textarea
        resize: none
        width: 100%
        height: 100%
        padding: 14px 20px 13px 20px
        font-size: 12px
        line-height: 18px
        color: #000000
    &__trigers
        grid-column: 1/3
        display: grid
        grid-template-columns: repeat(2, auto)
        column-gap: 35px
    &__btn
        border: none
        font-weight: 700
        font-size: 14px
        line-height: 21px
        color: #000000
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15)
    &__privacy
        display: flex
        align-items: center
        font-size: 12px
        line-height: 18px
        color: #000000
        position: relative
    &__accept
        margin-right: 10px
    &__privacy-link
        text-decoration: none
label.error
    @include form-label
    bottom: -9px
    color: red