@media (max-width: 1439px)
    .promo
        &__socials
            display: none
    .back-link
        width: 30px
        height: 30px
        right: 20px



@media (max-width: 1199px)
    .container
        max-width: 960px
    .about
        &__area
            grid-template-columns: 400px 510px
            column-gap: 30px
        &__ability
            grid-template: repeat(2,minmax(116px,auto))/repeat(2,245px)
            gap: 20px 20px
        &__ability-item
            grid-template-columns: 40px 195px
            column-gap: 10px
    .tools
        &__wrapper
            grid-template: repeat(auto-fit,minmax(270px,auto))/repeat(auto-fit,270px)
        &__usage
            grid-template: 70px/repeat(auto-fit,350px)
    .works
        &__wrapper
            grid-template: repeat(2,235px)/repeat(4,235px)
            grid-auto-rows: 235px
    .price
        &__wrapper
            width: 100%
            justify-content: space-around
    .contacts
        &__wrapper
            padding: 0
            grid-template-columns: 400px 540px
        &__form
            padding-right: 10px
            grid-template: 45px 190px 45px / repeat(2, 260px)



@media (max-width: 991px)
    .container
        max-width: 720px
    .hamburger
        cursor: default
    .promo
        &__link-item
            cursor: default
    .menu
        &__close
            cursor: default
        &__link
            cursor: default
            &:hover
                color: $second-color
        &__social-link
            cursor: default
    .btn
        &:hover
            background-color: $main-color
            box-shadow: none
    .title
        &_fz36
            font-size: 28px
            line-height: 40px
    .about
        padding-top: 80px
        &__area
            grid-template-columns: 320px 390px
            column-gap: 20px
        &__ability
            grid-template: repeat(3, minmax(50px, auto)) / 390px
            grid-auto-rows: minmax(50px, auto)
            row-gap: 20px
        &__ability-item
            grid-template-columns: 40px 330px
            column-gap: 20px
    .experience
        padding-top: 80px
        &__wrapper 
            margin: 60px 0 0 0
            grid-template-columns: repeat(2,1fr)
            column-gap: 20px
    .tools
        padding-top: 80px
        &__usage
            margin-top: 80px
        &__usage-title
            margin-top: 6px 
    .back-link
        visibility: hidden
    .works
        padding-top: 80px
        &__wrapper 
            grid-template: repeat(2,236px)/repeat(3,236px)
            grid-auto-rows: 236px
        &__link-item
            transition: none
            cursor: default
            &:hover
                transform: none
    .vertical
        grid-column: unset
        grid-row: unset
    .horizontal
        grid-column: unset
        grid-row: unset
    .price
        &__wrapper
            grid-template-columns: repeat(auto-fit, 366px)
    .contacts
        padding: 80px 0 92px 0
        &__socials-link
            cursor: default
        &__wrapper
            grid-template-columns: 320px 380px
        &__form
            padding-right: 10px
            grid-template: 45px 190px 45px / repeat(2, 180px)
        &__trigers
            grid-template: repeat(2,auto)/auto
            row-gap: 20px
            justify-items: center
        &__btn
            grid-row: 2/3
            box-shadow: none
        &__privacy-link
            cursor: default
            



@media (max-width: 767px)
    .container
        max-width: 540px
    .promo
        &__title
            width: 300px
            font-size: 32px
            line-height: 46px
        &__links
            margin-top: 30px
    .about
        padding-top: 40px
        &__area
            grid-template: 340px auto / 1fr
            row-gap: 40px
            justify-content: center 
        &__column
            justify-self: center
        &__photo
            width: 300px
            height: 100%
        &__ability
            margin-top: 40px
            grid-template: repeat(3,minmax(50px,auto))/1fr
            grid-auto-rows: minmax(50px,auto)
            row-gap: 20px
        &__ability-item
            grid-template-columns: 40px 1fr
            column-gap: 20px
        &__ability-circle
            padding: 15px 0 0 15px
            &::before
                width: 36px
                height: 36px
    .experience
        &__item 
            padding: 0 5px 0 20px
        &__circle
            &::after
                width: 20px
                left: -20px
    .tools
        padding-top: 80px
        &__wrapper
            margin-top: 40px
            grid-template: repeat(auto-fit,minmax(266px,auto))/repeat(auto-fit,266px)
        &__usage
             margin-top: 60px
             justify-content: center
    .works
        &__wrapper
            margin-top: 40px
            grid-template: repeat(3,265px)/repeat(2,265px)
            grid-auto-rows: 265px
            justify-content: center
    .price
        &__wrapper
            margin: 20px auto 0 auto
    .contacts
        padding: 40px 0 50px 0
        &__wrapper
            grid-template-columns: 1fr
        &__column
            &:first-child
                display: none 
        &__form
            padding-right: 0
            grid-template: 45px 190px 45px/repeat(2,260px)
        


@media (max-width: 575px)
    .container
        max-width: 100%
        padding: 0 10px 0 10px
    .promo
        &__title
            width: 250px
            font-size: 26px
            line-height: 39px
    .hamburger
        right: 20px
    .menu
        &__area
            width: 250px
            padding-left: 55px
    .title
        &_fz36
            font-size: 24px
            line-height: 40px
    .about
        padding-top: 40px
        &__area
            grid-template: 340px auto / 1fr
            row-gap: 40px
            justify-content: center 
        &__ability
            margin-top: 20px
            grid-template: repeat(3,minmax(116px,auto))/1fr
            grid-auto-rows: minmax(116px,auto)
            row-gap: 20px
        &__ability-item
            grid-template-columns: 40px 200px
            column-gap: 20px
        &__ability-circle
            padding: 15px 0 0 15px
            &::before
                width: 36px
                height: 36px
    .experience
        padding-top: 40px
        &__wrapper 
            margin: 40px 0 0 0
            grid-template: repeat(2, auto)/ 1fr
            row-gap: 40px
        &__item 
            margin-top: 10px
            padding: 0 15px 0 43px
            &::before
                height: calc( 100% + 10px )
                top: -18px
        &__circle
            &::after
                width: 42px
                left: -42px
    .tools
        padding-top: 40px
        &__wrapper
            margin-top: 40px
            grid-template: repeat(auto-fit,minmax(270px,auto))/repeat(auto-fit,270px)
        &__usage
             margin-top: 40px
             grid-template: 70px / 300px
    .works
        padding-top: 40px
        &__wrapper
            margin-top: 40px
            grid-template: 280px / 280px
            grid-auto-rows: 280px
            justify-content: center
    .price
        padding-top: 40px
        &__wrapper
            margin: 20px auto 0 auto
            grid-template-columns: 300px
        &__list-item
            margin-bottom: 20px
    .contacts
        &__form
            grid-template: repeat(2, 45px) 190px 90px / 300px
            justify-content: center
        &__item-text
            grid-column: unset
        &__trigers
            grid-column: unset

